<template>
  <div class="ueditor">
    <div class="ueditor-center" v-if="editorRemark" >
      <!-- 图片上传组件辅助-->
      <el-upload
        class="avatar-uploader"
        :action="serverUrl"
        name="img"
        :headers="header"
        :show-file-list="false"
        :on-success="uploadSuccess"
        :on-error="uploadError"
        :before-upload="beforeUpload">
      </el-upload>
      <el-row class="ueditor-center-row">
        <quill-editor ref="myQuillEditor"
                      v-model="content"
                      @ready="onEditorReady($event)"
                      :config="editorOption"
                      @change="onEditorChange($event)">
        </quill-editor>
        <span class="insert-toolTip" v-if="tipImg">{{tipText}}</span>
      </el-row>
      <div class="clearfix list-button" style="margin: 10px 0">
        <div class="pull-right" style="margin-right: 20px">
          <el-button   @click="onEditorBlur" >编辑完成</el-button>
        </div>
      </div>
    </div>
    <div class="list-remarks" @click="editorTextarea(editorRemark)" v-else>
      <div v-html="content"></div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'ueditor',
    data () {
      return {
        content: '<h2>I am Example</h2><h2>I am Example</h2><h2>I am Example</h2>',
        editorOption: { // 富文本编辑器配置
          // something config
        },
        editorRemark: true,
        quillUpdateImg: require('@/assets/img/loadingImg.gif'), // 根据图片上传状态来确定是否显示loading动画，刚开始是false,不显示
        serverUrl: 'https://jsonplaceholder.typicode.com/posts/',  // 这里写你要上传的图片服务器地址
        header: {token: sessionStorage.token},  // 有的图片服务器要求请求头需要有token之类的参数，写在这里
        tipImg: false,
        tipText: ''
      }
    },
    computed: {
      editor () {
        return this.$refs.myQuillEditor.quillEditor
      }
    },
    methods: {
      onEditorBlur (editor) {
//        console.log('editor blur!', editor)
        if (this.$route.query.type === 'detail') {
          this.editorRemark = !this.editorRemark
        }
//        editor.container.parentNode.focus()
      },
      onEditorFocus (editor) {
//        console.log('editor focus!', editor)
//        let toolbar = editor.getModule('toolbar').container
//        event.preventDefault()
      },
      onEditorReady (editor) {
        var toolbar = editor.getModule('toolbar')
        toolbar.addHandler('image', function () {
          document.querySelector('.avatar-uploader input').click()
        })
//        console.log('editor ready!', editor)
//        let $this = this
//        let conbar = editor.container.parentNode
//        conbar.setAttribute('tabindex', '0')
//        conbar.onblur = function (event) {
//          console.log(event)
//          if ($this.$route.query.type === 'detail') {
//            $this.editorRemark = !$this.editorRemark
//          }
//        }
//        let toolbar = editor.getModule('toolbar').container
//        toolbar.onmousedown = function (event) {
//          console.log(event)
//          event.preventDefault()
//        }
//        toolbar.onmouseup = function (event) {
//          console.log(event)
//          event.preventDefault()
//          event.stopPropagation()
//        }
//        toolbar.onclick = function (event) {
//          console.log(event)
//          event.preventDefault()
//        }
//        console.log(toolbar.getElementsByClassName('ql-video')[0])
//        toolbar.getElementsByClassName('ql-video')[0].addEventListener('mousedown', function (event) {
//          console.log(event)
//          event.preventDefault()
//          $this.mouse = false
//        }, false)
      },
      onEditorChange ({ editor, html, text }) {
        // console.log('editor change!', editor, html, text)
        this.content = html
      },
      editorTextarea (value) {
        if (this.$route.query.type === 'detail') {
          this.editorRemark = !value
        }
      },
      // 上传图片前
      beforeUpload (res, file) {
        // 显示loading动画
        // 获取富文本组件实例
        let quill = this.$refs.myQuillEditor.quill
        // 获取光标所在位置
        let length = quill.getSelection().index
        // 插入图片
        quill.insertEmbed(length, 'image', this.quillUpdateImg)
        // 调整光标到最后
        quill.setSelection(length + 1)
      },
      // 上传图片成功
      uploadSuccess (res, file) {
        console.log(res)
        // res为图片服务器返回的数据
        // 如果上传成功
        if (res.code === '200' && res.info !== null) {
          // 获取富文本组件实例
          let quill = this.$refs.myQuillEditor.quill
          // 获取光标所在位置
          let length = quill.getSelection().index
          // 插入图片  res.info为服务器返回的图片地址
          quill.insertEmbed(length, 'image', res.info)
          // 调整光标到最后
          quill.setSelection(length + 1)
        } else {
          this.uploadError()
        }
      },
      // 上传图片失败
      uploadError (res, file) {
        // loading动画消失
        let quill = this.$refs.myQuillEditor.quill
        // 获取光标所在位置
        let length = quill.getSelection().index
        // 插入图片
        quill.deleteText(length - 1, 1)
        // 调整光标到最后
        quill.setSelection(length - 1)
//        this.$message.error('图片插入失败')
        let $this = this
        $this.tipImg = true
        $this.tipText = '图片插入失败'
        window.setTimeout(function () {
          $this.tipImg = false
          $this.tipText = ''
        }, 1000)
      }
    },
    mounted () {
      // you can use current editor object to do something(editor methods)
//      console.log('this is my editor', this.editor)
      // this.editor to do something...
    },
    created (editor) {
      if (this.$route.query.type === 'detail') {
        this.editorRemark = false
      }
    }
  }
</script>



